var fn = () => console.log();

function *foo() {
    console.log("Hi.");
    true ? console.log("Hi.") : console.log("nuuu.");
    yield true ? fn() : console.log("nuuu.");
    yield 123;
    console.log("Hi. 2");
    yield true ? fn() : null;
}

var async = async function() {
    var prom = new Promise(resolve => resolve());
    await prom;
}

async();

foo().next();
if (true) {
    true ? console.log("Hi.") : null;
}
else {
    console.log("nuuu.");
}
